$dark-theme: "dark-theme";
$light-theme: "light-theme";

$navbar-col: "navbar-col";
$navbar-text-col: "navbar-text-col";
$navbar-children-col: "navbar-children-col";
$navbar-border-col: "navbar-border-col";
$page-col: "page-col";
$page-text-col: "$page-text-col";
$link-col: "link-col";
$hover-col: "hover-col";
$footer-col: "footer-col";
$footer-text-col: "footer-text-col";
$footer-link-col: "footer-link-col";
$footer-border-col: "footer-border-col";
$code-col: "code-col";
$code-bg-col: "code-bg-col";
$post-preview-text-col: "post-preview-text-col";
$post-preview-border-col: "post-preview-border-col";
$post-preview-meta-col: "post-preview-meta-col";
$post-tags-col: "post-tags-col";
$post-tags-hover-col: "post-tags-hover-col";
$table-nth-child-col: "table-nth-child-col";
$table-border-col: "table-border-col";

$themes: (
    $light-theme: (
        $navbar-col: #F5F5F5,
        $navbar-text-col: #404040,
        $navbar-children-col: #F5F5F5,
        $navbar-border-col: #EAEAEA,
        $page-col: #FFFFFF,
        $page-text-col: #404040,
        $link-col: #404040,
        $hover-col: #dc971f,
        $footer-col: #F5F5F5,
        $footer-text-col: #777777,
        $footer-link-col: #404040,
        $footer-border-col: #EAEAEA,
        $code-col: #c7254e,
        $code-bg-col: #f9f2f4,
        $post-preview-text-col: #404040,
        $post-preview-border-col: #eee,
        $post-preview-meta-col: #808080,
        $post-tags-col: #999,
        $post-tags-hover-col: #eee,
        $table-nth-child-col: #f8f8f8,
        $table-border-col: #ccc,
    ),
    $dark-theme: (
        $navbar-col: #242526,
        $navbar-text-col: #e4e6eb,
        $navbar-children-col: #F5F5F5,
        $navbar-border-col: #393a3b,
        $page-col: #18191a,
        $page-text-col: #e4e6eb,
        $link-col: #e4e6eb,
        $hover-col: #dc971f,
        $footer-col: #242526,
        $footer-text-col: #aaa,
        $footer-link-col: #dfdfdf,
        $footer-border-col: #242526,
        $code-col: #c7254e,
        $code-bg-col: #2b2829,
        $post-preview-text-col: #e4e6eb,
        $post-preview-border-col: #444,
        $post-preview-meta-col: #999,
        $post-tags-col: #e4e6eb,
        $post-tags-hover-col: #2b2829,
        $table-nth-child-col: #28292a,
        $table-border-col: #666,
    )
);

@mixin themeProperty($theme, $property, $color, $additionalPropertiesPositionIsFront, $additionalProperties) {
    @if $additionalPropertiesPositionIsFront {
        #{$property}: unquote($additionalProperties + ' ' + map-get($theme, $color));
    } @else {
        #{$property}: unquote(map-get($theme, $color) + ' ' + $additionalProperties);
    }
}

@mixin theme($property, $color, $additionalPropertiesPositionIsFront: false, $additionalProperties: '') {
    $light: map-get($themes, $light-theme);
    $dark: map-get($themes, $dark-theme);

    @include themeProperty($light, $property, $color, $additionalPropertiesPositionIsFront, $additionalProperties);

    @media (prefers-color-scheme: dark) {
        @include themeProperty($dark, $property, $color, $additionalPropertiesPositionIsFront, $additionalProperties);
    }
}
