/* Overwrites */
.post-image img {
    object-fit: cover;
}

/* preserve aspect ratio if 'dimension_attributes: true' */
/*picture > img {
    height: auto;
    width: auto;
}*/

$fa-logo-size: 2.4em;
.navbar-brand .fa-fw {
    font-size: $fa-logo-size;
}
a.navbar-brand {
    margin-top: -$fa-logo-size/4;
}

.navbar-brand span {
    vertical-align: middle;
}

code {
    @include theme('color', $code-col);
    @include theme('background-color', $code-bg-col);
}

/* Custom Styles */
.cryptos img,
article img {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.img-responsive.max-70-percent {
    max-width: 70%;
}

.page-image {
    margin-bottom: 60px;
}

.page-image.small > picture > img,
.page-image.small > img {
    max-height: 80px;
}

.page-image.medium > picture > img,
.page-image.medium > img {
    max-height: 120px;
}

.page-image.large > picture > img,
.page-image.large > img {
    max-height: 150px;
}

.page-image.xlarge > picture > img,
.page-image.xlarge > img {
    max-height: 200px;
}

.page-image.center > picture > img,
.page-image.center > img {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.container .imprint a,
.container .blog-post a {
    text-decoration: underline;
}


.about-text {
    position: relative;
    margin-left: 40px;
}

.about-icon {
    font-size: 24px;
    position: absolute;
    left: -40px;
    margin-top: 5px;
}

.nav-search-link .nav-search-icon {
    display: none;
}
.nav-search-link .nav-search-text {
    display: inline;
}
@media only screen and (min-width: 768px) {
    .nav-search-link .nav-search-icon {
        display: inline;
    }
    .nav-search-link .nav-search-text {
        display: none;
    }
}

#google-custom-search {
    margin-top: 50px;
}

#google-custom-search tr,
#google-custom-search td {
    border-style: none;
}

input.gsc-input,
.gsc-input-box,
.gsc-input-box-hover,
.gsc-input-box-focus,
.gsc-search-button,
.gsc-selected-option-container {
    box-sizing: content-box;
    line-height: normal;
}

.gsc-input-box {
    height: 40px !important;
}

.cryptos {
    text-align: center;
    margin-top: 40px;
}


.cryptos p.address {
    font-size: 9px;
    word-wrap: break-word;
    margin: 10px;
}

@media only screen and (max-width: 767px) {
    .cryptos p.address {
        margin-bottom: 50px;
        font-size: 14px;
    }
}

.cryptos img {
    margin-bottom: 15px;
}

.cryptos .ltc-icon {
    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 54px;
    line-height: 1;
    margin: 0;
}

.svg-litecoin {
    display: inline-block;
    height: 1em;
    overflow: visible;
    vertical-align: -.125em;
    font-size: 3em;
}

/* Contact
**************************************************/
#gmap,.gmap{
    min-height: 400px;
}
#gmap.gmap-full,.gmap.gmap-full{
    height: 100%;
}
#map-marker i{
    display: block;
    font-size: 70px;
    color: #363636;
}

form#contact-form{
    position: relative;
}
#alert-wrapper{
    position: absolute;
    top:0;
    left: 10%;
    opacity: 0;
    padding: 10px;
    background: rgba(255,0,0,0.3);
    color: #fff;
    width: 80%;
    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}
#alert-wrapper button{
    color:#fff;
    opacity:0.5;
}
#alert-wrapper .alert{
    padding:0;
    margin:10px;
}

.contact-detail{
    color: #838383;
    border-bottom: 1px solid #111;
    padding-bottom: 15px;
    margin-bottom: 15px;
    box-shadow: 0 1px 0 0 #272727;
}
.contact-detail h5{
    //font-family: 'ralewaythin';
    color: #dc971f;
}
.contact-detail:last-child{
    border-bottom: none;
    box-shadow: none;
    padding-bottom: 0;
    margin-bottom: 0;
}

.contact-detail .social-icons{
    margin-top: 20px;
}

.contact-detail .social-icons li a{
    font-size: 18px;
    padding: 7px;
    color: #8B8B8B;
}
.contact-detail .social-icons li a:first-child{
    padding-left: 0;
}
.contact-detail .social-icons li a:hover,
.contact-detail .social-icons li a:focus {
    color:#CECECE;
}



/* --- Buttons --- */

.btn-beautiful {
    font-family: 'Open Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 800;
    letter-spacing: 1px;
    padding: 10px 5px;
    @include theme('background-color', $page-col);
    border-radius: 0;
    @include theme('color', $page-text-col);
    border: 1px solid #ddd;
}
@media only screen and (min-width: 768px) {
    .btn-beautiful {
        padding: 15px 25px;
    }
}
.btn-beautiful:hover,
.btn-beautiful:focus {
    color: #FFF;
    border: 1px solid $hover-col;
    background-color: $hover-col;
}

footer a .fa-inverse {
    @media(prefers-color-scheme: dark) {
        color: #272727;
    }
}

.picture-bot {
    margin-bottom: 20px;
}

.qr-code {
    max-width: 155px;
}
